import { __ } from '@wordpress/i18n';
import { InspectorControls, useBlockProps } from '@wordpress/block-editor';
import { ColorPalette, PanelBody } from '@wordpress/components';
import ServerSideRender from '@wordpress/server-side-render';

import './editor.scss';

export default function Edit({ attributes, name, isSelected, setAttributes }) {

  const blockProps = useBlockProps({
    className: 'my-random-classname',
  });

  const colors = color_block_colors.map(color => {
    return {
      name: color.name,
      color: '#' + color.hex
    }
  });

  const getColor = (id) => {
    const match = color_block_colors.filter(color => color.id === id);
    return '#' + match[0].hex;
  };

  const setColor = (hex) => {

    hex = hex.replace('#', '');

    const match = color_block_colors.filter(color => color.hex === hex);
    const id = match[0].id;

    setAttributes({ color: id });
  };

  console.log(isSelected)

  return (
    <div { ...useBlockProps() }>
      {isSelected && <InspectorControls>
        <PanelBody title={__('Color', 'color-block')} initialOpen={true}>
          <ColorPalette
            clearable={false}
            disableCustomColors={true}
            colors={colors}
            value={getColor(attributes.color)}
            onChange={setColor}
          />
        </PanelBody>
      </InspectorControls>}

      <ServerSideRender
        block={name}
        attributes={attributes}
      />
    </div>
  );
}
